import React from 'react'
import { toast } from "react-toastify";
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import BottomBackNextBtn from 'views/mobileView/components/atoms/BottomBackNextBtn';
import ProgressBar from 'react-bootstrap/ProgressBar';
import db from 'libs/db';
import cssVar from 'views/mobileView/styles';
import AdminServices from 'services/api/AdminServices';
import { baseImgURL } from 'assets/Constant';

const DocumentUpload = (props) => {
    const taxStatus = props.location.state?.taxStatus

    const [panpic, setpanpic] = React.useState('');
    const [panID, setpanID] = React.useState('');

    const [chequepic, setchequepic] = React.useState('');
    const [chequeID, setchequeID] = React.useState('');
    var inputFilePan = React.useRef(null)
    var inputFileChe = React.useRef(null)
    const [user, setUser] = React.useState({});

    const [nextDisabled, setNextDisabled] = React.useState(false)
    const [loading, setloading] = React.useState();
    const [modalType, setmodalType] = React.useState('lump');

    const [ShowComment, setModelComment] = React.useState(false);
    React.useEffect(() => {
        if(!props.location.state?.taxStatus){
			props.history.push('/activate-direct-mf/tax-status')
		} else {
            db.get(db.model.userData)
            .then((u) => {
                if (u) {
                    if (u.doc) {
                        setpanpic(u.doc.panpic || '');
                        setchequepic(u.doc.chequepic || '');
                        setpanID(u.doc.panID || '');
                        setchequeID(u.doc.chequeID || '');
                    }
                }
            })
            .then(() => {
                db.get(db.model.user).then((p) => {
                    setUser(p.userData);
                    setloading(false);
                });
            });
        }
    }, []);

    React.useEffect(() => {
        checkValidation();
    }, [panID, chequeID]);

    const checkValidation = () => {
        if (
            panID !== ''
            && panID !== null
            && chequeID !== ''
            && chequeID !== null
        ) {
            setNextDisabled(false);
        } else {
            setNextDisabled(true);
        }
    };
    const uploadImage = async (singleFile, modalType) => {
        // Check if any file is selected or not
        if (singleFile != null) {
            // If file selected then create FormData
            const fileToUpload = singleFile;
            const data = new FormData();
            data.append('file', fileToUpload);

            //   var res = await AdminServices.uploadDoc(user.id,data).then((res) => {return res})
            //  console.log(modalType,res.data)

            try {
                const res = await AdminServices.uploadDoc(user.id, data).then((res) => res);
                console.log(res.data);
                if (res.type) {
                    if (res.data.DocumentId) {
                        if (modalType === 'pan') {
                            setpanID(res.data.DocumentId);
                        } else {
                            setchequeID(res.data.DocumentId);
                        }
                    } else {
                        console.log('ERR : 2', res.msg);
                        toast.error("try again...")
                    }
                } else {
                    console.log('ERR : 1', res.msg);
                    toast.error("try again...")

                }
            } catch (error) {
                console.log('ERR : c', error);
                toast.error("try again...")

            }
        } else {
            // if no file selected the show alert
            alert('Please Select File first');
        }
    };

    const imagePick = (event) => {
        if (modalType === 'pan') {
            setpanpic(URL.createObjectURL(event.target.files[0]));
            var file = event.target.files[0];

            uploadImage(file, modalType);
            setModelComment(false);
        } else {
            setchequepic(URL.createObjectURL(event.target.files[0]));

            var file = event.target.files[0];

            uploadImage(file, modalType);
            setModelComment(false);
        }
    };
    return (
        <div className='mainContainer'>
            <MainHeader
                header="Activate direct mutual funds"
                navigation={props.history}
            />
            <ProgressBar now={90} className="progress-bar-custom" />

            <div className="dataContainer" style={{ paddingTop: '42px' }}>
                <div className="header1dark" style={{ marginBottom: "4%" }}>{taxStatus === '1' ? "Upload legal guardian PAN" : "Upload PAN"}</div>
                {panpic === '' || panpic === null ? (
                    <div
                        style={{
                            borderWidth: 1,
                            borderStyle: 'dashed',
                            borderColor: cssVar.getPropertyValue("--secondaryGreen"),
                            borderRadius: 10,
                            backgroundColor: cssVar.getPropertyValue("--lightest"),
                            alignItems: 'center',
                            padding: "30px",
                            display: "flex",
                            justifyContent: "space-around"
                        }}
                        onClick={() => {
                            inputFilePan.current.click();
                            setModelComment(true);
                            setmodalType('pan');
                        }}>
                        <>
                            <input type='file' accept="image/*" id='panfile' ref={inputFilePan} style={{ display: 'none' }} onChange={(event) => imagePick(event)} />

                            <span className='bodyTextLight'
                                style={{
                                    color: cssVar.getPropertyValue("--secondaryGreen")
                                }}>
                                Take a photo or upload an image
                            </span>
                            <img
                                style={{
                                    color: cssVar.getPropertyValue("--secondaryGreen")
                                }}
                                src={baseImgURL+"cloud.svg"} />
                        </>
                    </div>
                ) : (
                    <div
                        style={{
                            borderWidth: 1,
                            borderStyle: 'dashed',
                            borderColor: cssVar.getPropertyValue("--secondaryGreen"),
                            borderRadius: 10,
                            backgroundColor: cssVar.getPropertyValue("--lightest"),
                            display: "flex",
                            flexDirection: "column",
                            justifyContent: "space-around",
                            height: "10px !important",


                        }}>
                        <img
                            width="100%" height="160"

                            style={{
                                borderRadius: 10
                            }}
                            src={panpic}

                        />
                        <div
                            style={{
                                alignItems: 'center',
                                padding: "10px",
                                display: "flex",
                                justifyContent: "space-around"
                            }}
                            onClick={() => {
                                inputFilePan.current.click();
                                setModelComment(true);
                                setmodalType('pan');
                            }}>
                            <>
                                <input type='file' accept="image/*" id='panfile' ref={inputFilePan} style={{ display: 'none' }} onChange={(event) => imagePick(event)} />

                                <span className='bodyTextLight'
                                    style={{
                                        color: cssVar.getPropertyValue("--secondaryGreen")
                                    }}>
                                    Upload a different image
                                </span>
                                <img
                                    style={{
                                        color: cssVar.getPropertyValue("--secondaryGreen")
                                    }}
                                    src={baseImgURL+"cloud.svg"} />
                            </>
                        </div>
                    </div>
                )}
                <div className="header1dark" style={{ marginTop: "10%", marginBottom: "4%" }}>Upload cancelled Cheque</div>

                {chequepic === '' || chequepic === null ? (
                    <div
                        style={{
                            borderWidth: 1,
                            borderStyle: 'dashed',
                            borderColor: cssVar.getPropertyValue("--secondaryGreen"),
                            borderRadius: 10,
                            backgroundColor: cssVar.getPropertyValue("--lightest"),
                            alignItems: 'center',
                            padding: "30px",
                            display: "flex",
                            justifyContent: "space-around"
                        }}
                        onClick={() => {
                            inputFileChe.current.click();
                            setModelComment(true);
                            setmodalType('cheque');
                        }}>
                        <>
                            <input type='file' accept="image/*" id='chequefile' ref={inputFileChe} style={{ display: 'none' }} onChange={(event) => imagePick(event)} />

                            <span className='bodyTextLight'
                                style={{
                                    color: cssVar.getPropertyValue("--secondaryGreen")
                                }}>
                                Take a photo or upload an image
                            </span>
                            <img
                                style={{
                                    color: cssVar.getPropertyValue("--secondaryGreen")
                                }}
                                src={baseImgURL+"cloud.svg"} />
                        </>
                    </div>
                ) : (
                    <div
                        style={{
                            borderWidth: 1,
                            borderStyle: 'dashed',
                            borderColor: cssVar.getPropertyValue("--secondaryGreen"),
                            borderRadius: 10,
                            backgroundColor: cssVar.getPropertyValue("--lightest"),
                            display: "flex",
                            flexDirection: "column",
                            justifyContent: "space-around",
                            height: "10px !important",


                        }}>
                        <img
                            width="100%" height="160"
                            style={{
                                borderRadius: 10
                            }}
                            src={chequepic}

                        />
                        <div
                            style={{
                                alignItems: 'center',
                                padding: "10px",
                                display: "flex",
                                justifyContent: "space-around"
                            }}
                            onClick={() => {
                                inputFileChe.current.click();
                                setModelComment(true);
                                setmodalType('cheque');
                            }}>
                            <>
                                <input type='file' accept="image/*" id='chequefile' ref={inputFileChe} style={{ display: 'none' }} onChange={(event) => imagePick(event)} />

                                <span className='bodyTextLight'
                                    style={{
                                        color: cssVar.getPropertyValue("--secondaryGreen")
                                    }}>
                                    Upload a different image
                                </span>
                                <img
                                    style={{
                                        color: cssVar.getPropertyValue("--secondaryGreen")
                                    }}
                                    src={baseImgURL+"cloud.svg"} />
                            </>
                        </div>
                    </div>
                )}

            </div>
            <div className='footer'>
                <BottomBackNextBtn
                    nextDisabled={nextDisabled}
                    navigation={props.history}
                    buttonText="Confirm"
                    next="/activate-direct-mf/sign-upload"
                    params={{ taxStatus: taxStatus }}
                    saveData={{
                        doc: {
                            panpic,
                            panID,
                            chequepic,
                            chequeID
                        }
                    }} />
            </div>
        </div>
    )
}

export default DocumentUpload
